export default {
	data() {
		return {
			inputMoneyValue: '',
			item: '',
			sex: '',
			gNumber: 0,
			mNumber: 0,
		}
	},
	onLoad(options) {
		this.item = JSON.parse(decodeURIComponent(options.item))
		// console.log(this.item)
		if(this.item.sex == 1) {
			this.sex = '男'
			this.mNumber = 1
			this.gNumber = 0
		}else{
			this.sex = '女'
			this.gNumber = 1
			this.mNumber = 0
		}
	},
	methods: {
		inputMoney(v) {
			this.inputMoneyValue = v.detail
		},
		sureAdd() {
			this.$http.post({
				url: '/v1/rest/manage/sceneChargeAddMember',
				data: {
					clubId: uni.getStorageSync('clubId'),
					gNumber: this.gNumber,
					mNumber: this.mNumber,
					payType: 2,
					productId: uni.getStorageSync('addActId'),
					totalPrice: this.inputMoneyValue,
					userId: this.item.userId
				}
			}).then(resp => {
				// console.log(resp)
				if(resp.status == 200) {
					uni.showToast({
						title: '添加成功！',
						duration: 2000,
						icon: 'none'
					});
					uni.navigateBack()
				}else{
					uni.showToast({
						title: '操作失败！',
						duration: 2000,
						icon: 'none'
					});
				}
			})
		}
	}
}
